
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import {detail, wallet} from '../config'
import VScroll from '../components/VScroll'
export default {
  name: 'balanceDetail',
  head: {
    title: {
      inner: ''
    }
  },
  components: {
    Group,
    VScroll,
    Cell
  },
  data () {
    return {
      balance: 0,
      list: [],
      form: {
        userId: '',
        type: 2,
        limit: 10,
        pageIndex: 0
      }
    }
  },
  mounted () {
    this.form.userId = this.$route.params.userId
    if (this.form.userId === 'null') {
      this.$router.replace('/login')
    } else {
      this.getList(() => {}, null)
    }
  },
  methods: {
    statusNoMore () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'none'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'block'
      })
    },
    statusLoad () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'block'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'none'
      })
    },
    statusInit () {
      this.$el.querySelectorAll('.load').forEach(el => {
        el.style.display = 'none'
      })
      this.$el.querySelectorAll('.no-more').forEach(el => {
        el.style.display = 'none'
      })
    },
    onRefresh (done) {
      this.form.pageIndex = 0
      this.statusInit()
      this.getList(done, 1)
    },
    onInfinite (done) {
      this.form.pageIndex ++
      if (this.list.length % this.form.limit) {
        this.statusNoMore()
      } else {
        this.$el.querySelectorAll('.load').forEach(el => {
          el.style.display = 'block'
        })
        this.getList(done, 0)
      }
    },
    getList (done, status) {
      const This = this
      this.$http({
        method: 'jsonp',
        url: detail,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form,
        before: () => {
          if (status) {
            this.list = []
          }
        }
      })
      .then(res => {
        res.body.data.scoreList.forEach(el => {
          This.list.push(el)
        })
        if (res.body.data.scoreList.length < This.form.limit) {
          this.statusNoMore()
        } else {
          this.statusLoad()
        }
        done()
        for (const i in this.list) {
          this.list[i].createTime = dateFormat(this.list[i].createTime)
        }
        this.$http({
          method: 'jsonp',
          url: wallet,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: {
            userId: this.form.userId
          }
        })
        .then(res => {
          this.balance = res.body.data.wallet.balance
        })
      })
    }
  }
}



